<template>
  <div class="businessProcess-top-style">
    <div class="top-main border-style">
      <div class="title">省派单及时率</div>
      <div
        class="charts-main"
        v-loading="!marketLineRes"
        element-loading-background="rgba(0, 0, 0, 0.2)"
        element-loading-text="加载中"
      >
        <bda-line-charts
          v-if="marketLineRes"
          :chartData="marketLineData"
          :xRotate="0"
          :yDw="dw"
        ></bda-line-charts>
      </div>
    </div>
    <div class="top-main border-style">
      <div class="title">地市派单及时率</div>
      <div
        class="charts-main"
        v-loading="!countyLineRes"
        element-loading-background="rgba(0, 0, 0, 0.2)"
        element-loading-text="加载中"
      >
        <bda-line-charts
          v-if="countyLineRes"
          :chartData="countyLineData"
          :xRotate="0"
          :yDw="dw"
        ></bda-line-charts>
      </div>
    </div>
  </div>
</template>

<script>
import Interface from '../../../base/mixin/service-index-control';
import bdaLineCharts from '../../../widgets/bda-line-charts/bda-line-charts';
//  import bda3dPie from '../../../widgets/bda-3d-pieCharts/bda-3d-pieCharts';
import qs from 'qs';
export default {
  name: "businessProcessTop",
  components: { bdaLineCharts },
  mixins: [Interface],
  data() {
    return {
      marketLineRes: false,
      marketLineData: {},
      countyLineRes: false,
      countyLineData: {},
      dw: '%'
    }
  },
  watch: {},
  created() {
  },
  mounted() {
    // 月业务量
    this.getSendOrdersForTown(qs.stringify({
      dimensionality: this.$parent.dateType,
      timeStr: this.$parent.dateParams
    })).then(res => {
      this.dispathFilter(res, '1');
    }).catch(() => {
      this.marketLineRes = true;
    });
    // 业务类型占比
    this.getSendOrdersForCounty(qs.stringify({
      dimensionality: this.$parent.dateType,
      timeStr: this.$parent.dateParams
    })).then(res => {
      //   window.console.log('res',res)
      this.dispathFilter(res, '2');
    }).catch(() => {
      this.countyLineRes = true;
    });
  },
  methods: {
    dispathFilter(data, type) {
      let xData = [], dataMap = {};
      if (data) {
        for (let key in data) {
          dataMap[key] = [];
          if (data[key]) {
            for (let item of data[key]) {
              item.value = item.per != (undefined || null) ? item.per : 0;
              dataMap[key].push(item.value);
              if (data[key].length !== xData.length) {
                xData.push(item.dates)
              }
            }
          }
        }
      }
      if (type === '1') {
        this.marketLineRes = false;
        this.marketLineData = {
          dimension: xData,
          dataMap: dataMap,
          //   color: ['#ea9a90', '#07c5f3', '#f76b92']
          color: ['#019eff', '#00FFD4']
        };
        this.$nextTick(() => {
          this.marketLineRes = true;
        })
      } else {
        this.countyLineRes = false;
        this.countyLineData = {
          dimension: xData,
          dataMap: dataMap,
          //   color: ['#ea9a90', '#07c5f3', '#f76b92']
          color: ['#019eff', '#00FFD4']
        };
        this.$nextTick(() => {
          this.countyLineRes = true;
        })
      }

    },
    businessForTypeFilter(data) {
      let _data = [];
      if (data) {
        for (let item of data) {
          item.value = item.ccsum;
          item.y = item.ccsum;
          _data.push(item);
        }
      }
      this.forTypeRes = false;
      this.forTypePie.data = _data;
      this.$nextTick(() => {
        this.forTypeRes = true;
      })
    }
  }
}
</script>

<style scoped lang="scss">
.businessProcess-top-style {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  .top-main {
    background: url("../../../assets/img/管理框1.png") no-repeat top center;
    background-size: 100% 100%;
    // padding: 10px 20px;
    // box-sizing: border-box;
    flex: 1;
    height: 100%;
    position: relative;
    float: left;
    font-size: 16px;
    .title {
      // background: linear-gradient(
      //   to bottom,
      //   rgba(15, 146, 229, 0.3),
      //   #061b3a
      // );
      color: #00e7ff;
      // line-height: 38px;
      height: 22%;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .charts-main {
      height: calc(100% - 22% - 40px);
      // box-sizing: border-box;
      width: calc(100% - 40px);
      position: relative;
      padding: 0 20px;
    }
  }

  //   .top-main:last-child {
  //     margin-left: 15px;
  //   }
}
</style>